.header {
    position: relative;
  display: flex;
  flex-direction: column;
   min-height: 66px;
    background: rgba(0, 0, 0, 0.3);

   }
.header--nav-opened {
  background: #283645;
    min-height: 66px;
}
.header__nav {
  height: 66px;
  width: 100%;
display: flex;
flex-direction: column;
}

 @media (min-width: @tab-width) {
 .header--nav-opened {
  min-height: 110px;
 }

 .header{
min-height: 110px;
}
}
 @media (min-width: @desk-width){
   .header {
   flex-direction: row;
    background: rgba(0, 0, 0, 0.3);
 }
    .header__nav {
      width: 200px;

    }
}
